.Sert {
  position: relative;
  text-align: center;
  overflow: visible;
  height: 100vh;
  background-color: lightpink;
  color: #fff;
}

.Sert-Title {
  padding-top: 50px;
}

.Sert-Wrapper {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
  overflow: visible;
  padding: 0px 20px;
}

.Sert-Left {
  width: 50%;
  display: flex;
  position: relative;
}

.Sert-Img {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 200px;
  height: 300px;
  border-radius: 10px;
  overflow: visible;
}

.Sert-ImgOne {
  background-image: url('../../img/sertificate/sert.jpg');
}

.Sert-ImgTwo {
  background-image: url('../../img/sertificate/sert2.jpg');
  margin: 20px 15px 0px;
}

.Sert-ImgThree {
  background-image: url('../../img/sertificate/sert3.jpg');
  margin-top: 40px;
}

.Sert-Right {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  ul {
    margin-top: 30px;
    margin-left: 20px;
    flex-direction: column;
    li {
      margin-bottom: 50px;
    }
  }
}

@media (max-width: 1200px) {
  .Sert {
    overflow: visible;
  }

  .Sert-Title {
    margin-bottom: 25px;
  }
  .Sert-Wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  .Sert-Left {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex: 1;
    .Sert-Img {
      position: static;
      margin-bottom: 15px;
      margin-top: 0;
    }
  }
  .Sert-Right {
    width: 100%;
    overflow: visible;
    padding-bottom: 30px;
    flex: 1;
    ul {
      li {
        margin-bottom: 15px;
      }
    }
  }
}
